<template>
  <div
    id="chart1"
    style="width:280px;height:200px;"
  ></div>
</template>
<script>
import echarts from "echarts";
export default {
  name: "chart1",
  props: {},
  data() {
    return {};
  },
  methods: {
    initChart() {
      var mixLineBar = echarts.init(document.getElementById("chart1"));
      const option = {
        title : {
            subtext: ':'
        },
       legend: {
            data:['','','','','','']
        },
        // 
        tooltip: {
          confine: true,
          trigger: "axis",
          axisPointer: {
            type: "cross",
            crossStyle: {
              color: "#fff"
            }
          }
        },
      
        textStyle: {
          color: "rgba(255, 255, 255)"
        },
        xAxis: {
          type: "category",
          data: ["1", "2", "3", "4", "5", "6"],
          color:"#fff",
          
        },
        yAxis: {
          type: "value"
        },
        series: [
          {
            data: [120, 200, 150, 80, 70, 96],
            type: "bar"
          }
        ]
      };
      
      mixLineBar.setOption(option);
      window.addEventListener("resize", function() {
        mixLineBar.resize();
      });
    }
  },
  mounted() {
    this.initChart();
  }
};
</script>
